
.tabs{position:relative;flex:1
}
.tabs .tab-list{flex:0 1 auto
}
.tabs .tab-content{flex-direction:column;overflow:hidden;position:relative;margin:10px;flex:1 1
}
.tabs .tab-pane{width:100%;flex:1 1
}
.tabs .tab-pane.is-active{transform:translateZ(0)
}
.tabs .tab-pane[class*="Out"]{overflow:hidden;position:absolute;top:0;left:0;right:0;bottom:0;transform:translateX(0);transform:translateY(0)
}
.tabs.is-layout-top{flex-direction:column
}
.tabs.is-layout-bottom{flex-direction:column-reverse
}
.tabs.is-layout-left{flex-direction:row;overflow-x:hidden;overflow-y:auto
}
.tabs.is-layout-left .tab-list{flex-direction:column;align-items:flex-start;max-width:calc(100% / 6);border-bottom:none;border-right:1px solid #b5b5b5
}
.tabs.is-layout-left li{width:100%
}
.tabs.is-layout-left li a{border-bottom:none;border-right:1px solid #b5b5b5;margin-bottom:0;margin-right:-1px;justify-content:flex-end
}
.tabs.is-layout-left li a:hover{border-right-color:#363636
}
.tabs.is-layout-left li.is-active a{border-right-color:#00d1b2
}
.tabs.is-layout-right{flex-direction:row-reverse;overflow-x:hidden;overflow-y:auto
}
.tabs.is-layout-right .tab-list{flex-direction:column;align-items:flex-end;justify-content:flex-start;max-width:calc(100% / 6);border-bottom:none;border-left:1px solid #b5b5b5
}
.tabs.is-layout-right .tab-list li{width:100%
}
.tabs.is-layout-right .tab-list li a{border-bottom:none;border-left:1px solid #b5b5b5;margin-bottom:0;margin-left:-1px;justify-content:flex-start
}
.tabs.is-layout-right .tab-list li a:hover{border-left-color:#363636
}
.tabs.is-layout-right .tab-list li.is-active a{border-left-color:#00d1b2
}



